/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
  overflow: hidden; /* Prevents scroll */
  background: linear-gradient(135deg, #1e1f26, #2c2f36); /* Soft dark gradient */
  color: #e0e0e0; /* Light text color for visibility */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header Styling */
.header {
  width: 100%;
  height: 130px;
  background-color: #18191f;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between; /* Aligns content to left and right */
  padding: 0 200px;
  box-sizing: border-box;
  font-size: 1.5rem;
  font-weight: bold;
  border-bottom: 1px solid #333;
  position: relative;
}

.header .hamburger-btn {
  font-size: 28px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  position: absolute;
  top: 50%; /* Aligns vertically in the center of the header */
  left: 20px; /* Controls the gap from the left side */
  transform: translateY(-50%); /* Centers it vertically */
  z-index: 1100;
}

/* Sidebar Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: -300px; /* Hidden initially */
  width: 300px;
  height: 100%;
  background-color: #18191f;
  box-shadow: 4px 0 12px rgba(0, 0, 0, 0.6);
  padding: 20px;
  transition: left 0.3s ease;
  z-index: 1000;
}

.sidebar-overlay.open {
  left: 0; /* Sidebar visible when open */
}

/* Sidebar Content */
.sidebar-content {
  color: #fff;
  padding-top: 80px; /* This controls the gap from the top of the sidebar */
}

/* Sidebar Buttons */
.sidebar-button {
  background-color: #3b3f47;
  color: white;
  border: none;
  padding: 12px;
  width: 100%;
  margin: 10px 0;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
}

.sidebar-button:hover {
  background-color: #4e535c;
}

/* Toggle Switch Styles */
.toggle-wrapper {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 34px;
}

.toggle-switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  border-radius: 50%;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Body Container */
.body-container {
  display: flex;
  height: calc(100vh - 130px); /* Adjust height to match the header height */
  overflow: hidden;
}

/* Right Panel Styling */
.right-panel {
  width: 30%; /* Adjust the width of the right panel */
  padding: 20px;
  background-color: #1e1f26; /* Set a different color from the left panel */
  box-sizing: border-box;
  overflow-y: auto; /* Allow scrolling if content overflows */
  color: #e0e0e0; /* Light text color for visibility */
}

.left-panel {
  width: 70%;
  padding: 20px;
  background-color: #2c2f36;
  box-sizing: border-box;
}

/* Picture Box for Video */
.picture-box {
  width: 90%; /* Takes up the full width */
  height: 720px; /* Fixed height */
  background-color: #444950;
  border-radius: 12px;
  margin: 30px auto 40px auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Align the video to the top */
  padding: 8px; /* Padding around the video */
  position: relative; /* Position relative to contain the spinner */
}

.picture-box video {
  width: 100%; /* Make the video width 100% of its parent */
  max-height: 100%; /* Ensure the video doesn't stretch beyond the container */
  border-radius: 20px;
  object-fit: contain;
  margin-top: 0; /* Remove top margin, video aligns with container top */
}

/* Floating Buttons */
.floating-buttons {
  position: relative;
  bottom: auto;
  left: auto;
  width: 100%;
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  z-index: 10;
  gap: 46px; /* Space between buttons */
}

.floating-button {
  background-color: #3b3f47;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  padding: 15px 30px;
  cursor: pointer;
  font-size: 1.1rem;
  transition: background-color 0.2s, box-shadow 0.2s;
  flex: 1;
  margin: 0 8px;
  max-width: 200px;
  flex-wrap: wrap;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); /* Shadow added */
}

.floating-button:hover {
  background-color: #4e535c;
}

/* Hamburger Button */
.hamburger-btn {
  font-size: 28px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  position: absolute;
  top: 5.5%; /* Aligns vertically in the center of the header */
  left: 20px; /* Controls the gap from the left side */
  transform: translateY(-50%); /* Centers it vertically */
  z-index: 1100;
}

/* Loading Container */
.loading-container {
  position: absolute; /* Position it inside the picture box */
  top: 50%; /* Vertically center it */
  left: 50%; /* Horizontally center it */
  right: 50%;
  transform: translate(-50%, -50%); /* Perfectly center */
  text-align: center; /* Ensures text is horizontally centered */
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3; /* Light gray background */
  border-top: 5px solid #2b2e35; /* Blue color for the spinner */
  border-radius: 50%;
  animation: spin 1s linear infinite; /* Spinning animation */
}

/* Spinner Animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Loading Text */
.loading-container p {
  margin-top: 20px;  /* Adjust text's margin from spinner */
  color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center; /* Center the text horizontally */
  margin-left: -15px;
}